.page {
  max-width: 1240px;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.title {
  composes: text text_type_main-large pl-3 pt-5 pb-5 from global;
}

.container {
  display: grid;
  grid-template-columns: repeat(2, 608px);
  gap: 60px;
  width: 100%;
  height: 100%;
}