.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 180px;
  width: 480px;
}

.title {
  composes: text text_type_main-medium pb-6 from global;
  margin: 0;
}

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form div {
	width: 100%;
}

.wrapper {
  composes: pb-6 from global;
}

.text {
  composes: text text_type_main-default text_color_inactive pt-20 pb-4 from global;
}

.link {
  composes: text text_type_main-default from global;
  padding-left: 8px;
}